const qrcode = 'data:image/png;base64,';
export default function Reward() {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [openMessageAlert, setOpenMessageAlert] = React.useState(false);
    const handleClick = () => {
        setOpenMessageAlert(true);
    };
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpenMessageAlert(false);
    };
    const handlePopoverOpen = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handlePopoverClose = () => {
        setAnchorEl(null);
    };
    const open = Boolean(anchorEl);
    const clipboard = new ClipboardJS('.copyBtn');
    clipboard.on('success', function(e) {
        handleClick()
    });
    clipboard.on('error', function(e) {
    });
    return (
        <div className={classes.root}>
            <Snackbar
                open={openMessageAlert}
                anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                autoHideDuration={1000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success">
                    Copy success
                </Alert>
            </Snackbar>
            <p>如果你觉得这个工具帮到你了，</p>
            <List component="nav" aria-label="main mailbox folders">
                {
                    rewardList.map(item=>{
                        return (
                            <ListItem key={item.name}>
                                <Accordion style={{width: '100%'}}>
                                    <AccordionSummary
                                        expandIcon={<img src={qrcode} onMouseEnter={handlePopoverOpen} onMouseLeave={handlePopoverClose}/>}
                                        aria-controls="panel1a-content"
                                        id="panel1a-header"
                                    >
                                        <img className={classes.icon} src={item.icon} alt=""/>
                                        <ListItemText className={classes.address} primary={item.address} />
                                        <FileCopyIcon className={classnames('copyBtn', classes.copy)} data-clipboard-text={item.address} />
                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <QRCode
                                            className={classnames('qrcode', classes.QRCode)}
                                            value={item.address}
                                            size={100}
                                        />
                                    </AccordionDetails>
                                </Accordion>
                            </ListItem>
                        )
                    })
                }
            </List>
        </div>
    );
}
